.logo {
  background-image: url(../../images/logo.svg);
  background-repeat: no-repeat;
  background-size: 142px 33px;

  width: 142px;
  height: 33px;
  object-fit: cover;
}

@media screen and (max-width: 879px) {
  .logo {
    background-size: 103.74px, 24.4px;
    margin-left: 27px;
    height: 24.4px;
  }
}